import {
	mapState,
	mapGetters,
	mapMutations,
	mapActions
} from 'vuex';
import $U from '@/common/unit';
import pageTitle from '@/components/pageTitle.vue';
import uniPopup from '@/components/uni-popup/uni-popup.vue';
export default {
	data() {
		return {
			// 各种状态
			listStatus: false,
			collectStatus: false,
			nightStatus: false
		}
	},
	filters: {
		// 时间戳转时间
		formatTime(num) {
			return num === 100 ? $U.formatTime(0) : $U.formatTime(num)
		}
	},
	components: {
		pageTitle,
		uniPopup
	},
	computed: {
		...mapState({
			durationTime: ({audio}) => audio.durationTime,
			currentTime: ({audio}) => audio.currentTime,
			audioList: ({audio}) => audio.audioList,
			playStatus: ({audio}) => audio.playStatus
		}),
		...mapGetters([
			'audioName',
			'singerName',
			'singerSynopsis',
			'CurCover'
		])
	},
	methods: {
		...mapActions([
			'PlayOrPause',
			'PreOrNext',
			'sliderToPlay',
			'selectPlay',
			'init'
		]),
		// 改变状态
		chageStatus(statusType) {
			this[statusType] = !this[statusType]
		},
		// 展示歌手简介详情
		showSingerSynopsis() {
			this.$refs.popup.open()
		}
	},
	onLoad() {
		this.init()
	}
}
